import React from 'react';
import { Switch, Route } from 'react-router-dom';
import './App.css';
import Presence from './pages/Presence';
import Home from './pages/Home';

function App() {
	return (
		<Switch>
			<Route exact path="/">
				<Home />
			</Route>
			<Route path="/presence">
				<Presence />
			</Route>
		</Switch>
	);
}

export default App;
